'use client'
import React from 'react'; 
import {
  MdSpaceDashboard, MdPeople, MdEvent, MdAttachMoney, MdGroups, MdVolunteerActivism,
  MdMessage, MdPermMedia, MdSettings, MdPerson
} from "react-icons/md";

const NAV_ITEMS = [
  { label: "Dashboard", icon: <MdSpaceDashboard />, view: "dashboard" },
  { label: "Profile", icon: <MdPerson />, view: "profile" },
  { label: "Members", icon: <MdPeople />, view: "members" },
  { label: "Attendance", icon: <MdEvent />, view: "attendance" },
  { label: "Events", icon: <MdEvent />, view: "events" },
  { label: "Donations", icon: <MdAttachMoney />, view: "donations" },
  { label: "Groups", icon: <MdGroups />, view: "groups" },
  { label: "Volunteers", icon: <MdVolunteerActivism />, view: "volunteers" },
  { label: "Communication", icon: <MdMessage />, view: "communication" },
  { label: "Media", icon: <MdPermMedia />, view: "media" },
  { label: "Settings", icon: <MdSettings />, view: "settings" },
];

const SideBarDash = ({ onMenuClick }) => {
  return (
    <div className='h-auto max-w-screen w-68 bg-white pt-10 rounded-lg m-1'>
      <div className='flex-col justify-center items-center w-64 flex'>
        <h1 className='text-2xl font-semibold text-persian-blue-700 cursor-none'>Main Menu</h1>
        <ul className="flex flex-col justify-center p-4">
          {NAV_ITEMS.map(({ label, icon, view }) => (
            <li
              key={label}
              onClick={() => onMenuClick(view)}
              className="
                transition-all duration-100 flex pl-5 items-center text-xl gap-3 p-4
                text-persian-blue-600 group hover:bg-persian-blue-600 hover:text-persian-blue-50
                hover:rounded-lg cursor-pointer
              "
            >
              <span className="group-hover:scale-110 transition-all duration-100">{icon}</span>
              <span className="group-hover:scale-105 transition-all duration-100">{label}</span>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default SideBarDash;
